import {
  Component,
  h,
  Element,
  State,
  Prop,
  Event,
  EventEmitter,
  Method,
  Fragment,
  FunctionalComponent,
  Host,
} from "@stencil/core";
import { RWCActionHelper } from "../../utils/actions/rwcActionHelper";
import CloseIcon from "@material-design-icons/svg/round/close.svg";
import { EventManager } from "../../utils/actions/eventManager";
import { IOpenable } from "../../interfaces/actions/IOpenable";
import { IClosable } from "../../interfaces/actions/IClosable";
import { IToggleable } from "../../interfaces/actions/IToggleable";

/**
 *
 * @slot header The slot for the header content of the modal.
 * @slot footer The slot for the footer content of the modal.
 * @slot - Default - The default slot for the main content of the modal.
 */
@Component({
  tag: "rwc-modal",
  styleUrl: "modal.scss",
  shadow: true,
})
export class RwcModal implements IOpenable, IClosable, IToggleable {
  /**
   * The reference to the currently open modal component.
   */
  static openModal: RwcModal = null;

  /**
   * The reference to the host element.
   */
  @Element() host: HTMLRwcModalElement;

  /**
   * The reference to the root element.
   */
  private rootElement?: HTMLDivElement;

  /**
   * The reference to the inner container of the modal.
   */
  private innerContainer?: HTMLDivElement;

  /**
   * Determines if the modal has content in the header slot.
   */
  private hasHeaderContent: boolean = false;

  /**
   * Determines if the modal has content in the footer slot.
   */
  private hasFooterContent: boolean = false;

  /**
   * Event manager used to manage component's event subscriptions.
   */
  public eventManager: EventManager = new EventManager();

  /**
   * List of the actions available for the component.
   */
  public actions: ("open" | "close" | "toggle")[] = ["open", "close", "toggle"];

  /**
   * The timeout callback of the modal animation.
   */
  private animationTimeout: NodeJS.Timeout = null;

  /**
   * Key used to bind action elements to the modal.
   */
  @Prop({ reflect: true })
  rwcKey: string = RWCActionHelper.getDefaultComponentKey("modal");

  /**
   * If content is longer than sidebar then scroll self instead of parent.
   */
  @Prop({ reflect: true }) innerScroll: boolean = false;

  /**
   * Sets rendering of divider after the header.
   */
  @Prop({ reflect: true }) renderHeaderDivider: boolean = true;

  /**
   * Sets rendering of divider before the footer.
   */
  @Prop({ reflect: true }) renderFooterDivider: boolean = true;

  /**
   * Sets rendering of close icon.
   */
  @Prop({ reflect: true }) renderCloseIcon: boolean = true;

  /**
   * Speed of the modal fade in/out animation.
   */
  @Prop() animationSpeed: number = 200;

  /**
   * The event called on completing the opening of the modal.
   */
  @Event({ eventName: "modalOpen" }) openEvent: EventEmitter<void>;

  /**
   * The event called on completing the closing of the modal.
   */
  @Event({ eventName: "modalClose" }) closeEvent: EventEmitter<void>;

  /**
   * The current open state of the modal.
   */
  @State() isOpen: boolean = false;

  /**
   * Get current opacity from style if set or from computed style otherwise.
   */
  get currentOpacity(): number {
    return Number.parseFloat(
      this.rootElement?.style.opacity ||
        getComputedStyle(this.rootElement).opacity
    );
  }

  /**
   * Gets the animation duration from style variables in milliseconds.
   */
  get animationDuration(): number {
    return Number.parseInt(
      getComputedStyle(this.host).getPropertyValue("--modal-animation-duration")
    );
  }

  /**
   * Initializes event listeners on insert into DOM.
   */
  connectedCallback(): void {
    this.init();
  }

  /**
   * Removes registered event listeners before removing from DOM.
   */
  disconnectedCallback(): void {
    this.eventManager.clearEventListeners();
  }

  /**
   * Called before each render call.
   */
  componentWillRender(): void {
    this.hasHeaderContent = Boolean(this.host.querySelector('[slot="header"]'));
    this.hasFooterContent = Boolean(this.host.querySelector('[slot="footer"]'));
  }

  /**
   * Initializes toggle / close / open events.
   */
  @Method()
  async init(): Promise<void> {
    this.eventManager.clearEventListeners();
    this.bindToggleActions();
  }

  /**
   * Toggles sidebar's open state.
   */
  @Method()
  async toggle(): Promise<void> {
    if (this.isOpen) {
      this.close();
    } else {
      this.open();
    }
  }

  /**
   * Opens sidebar.
   */
  @Method()
  async open(): Promise<void> {
    if (!this.isOpen) {
      if (RwcModal.openModal !== null) {
        await RwcModal.openModal.close();
      }

      clearTimeout(this.animationTimeout);

      this.isOpen = true;

      this.animationTimeout = setTimeout(() => {
        RwcModal.openModal = this;
        document.body.classList.add("modal-open");
        this.openEvent.emit();
      }, this.animationDuration);
    }
  }

  /**
   * Closes the modal.
   */
  @Method()
  async close(): Promise<void> {
    if (this.isOpen) {
      clearTimeout(this.animationTimeout);

      this.isOpen = false;

      setTimeout(() => {
        if (RwcModal.openModal === this) {
          RwcModal.openModal = null;
          document.body.classList.remove("modal-open");
        }
        this.closeEvent.emit();
      }, this.animationDuration);
    }
  }

  /**
   * Bind toggle events to the elements matching this component's key.
   */
  bindToggleActions(): void {
    RWCActionHelper.bindRWCActions(this, this.host, this.actions, this.rwcKey);
  }

  /**
   * Handle falloff click (background area of modal window).
   */
  falloffClick(event: PointerEvent): void {
    if (
      !this.innerContainer.contains(event.target as HTMLElement) &&
      !this.host.contains(event.target as HTMLElement)
    ) {
      this.close();
    }
  }

  /**
   * Get header html if header content is set.
   */
  private renderHeaderHtml(): string {
    if (this.hasHeaderContent) {
      return (
        <Fragment>
          <div class={{ header: true, flex: this.renderCloseIcon }}>
            <div>
              <slot name="header"></slot>
            </div>
            {this.renderCloseIcon ? this.renderCloseIconHtml() : ""}
          </div>
          {this.renderHeaderDivider ? <div class="divider"></div> : ""}
        </Fragment>
      );
    } else if (this.renderCloseIcon) {
      return <div class="header empty">{this.renderCloseIconHtml()}</div>;
    }
    return "";
  }

  /**
   * Renders the footer if footer content is set.
   */
  private renderFooterHtml(): FunctionalComponent {
    if (this.hasFooterContent) {
      return (
        <Fragment>
          {this.renderFooterDivider ? <div class="divider"></div> : ""}
          <div class="footer">
            <slot name="footer"></slot>
          </div>
        </Fragment>
      );
    }
    return <Fragment></Fragment>;
  }

  /**
   * Renders the close icon if rendering of close icon is enabled.
   */
  private renderCloseIconHtml(): FunctionalComponent {
    if (this.renderCloseIcon) {
      return (
        <Fragment>
          <div class="close-wrapper">
            <div
              class="close"
              onClick={() => this.close()}
              innerHTML={CloseIcon}
            ></div>
          </div>
        </Fragment>
      );
    }
    return <Fragment></Fragment>;
  }

  /**
   * Renders the component.
   */
  render(): FunctionalComponent {
    return (
      <Host>
        <div
          ref={(element: HTMLDivElement) => (this.rootElement = element)}
          class={{
            container: true,
            open: this.isOpen,
            "inner-scroll": this.innerScroll,
          }}
          onClick={(event: PointerEvent) => this.falloffClick(event)}
        >
          <div class="scroll-wrapper">
            <div class="inner-container-wrapper">
              <div
                class="inner-container"
                ref={(element: HTMLDivElement) =>
                  (this.innerContainer = element)
                }
              >
                {this.renderHeaderHtml()}

                <div class="content">
                  <slot></slot>
                </div>

                {this.renderFooterHtml()}
              </div>
            </div>
          </div>
        </div>
      </Host>
    );
  }
}
